module.exports = "import {Component, OnInit} from '@angular/core';\r\nimport {NzFormatEmitEvent, NzTreeNode} from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-select-checkable',\r\n  template: `\r\n    <nz-tree-select\r\n      style=\"width: 250px\"\r\n      [nzNodes]=\"nodes\"\r\n      nzPlaceHolder=\"Please select\"\r\n      [(ngModel)]=\"value\"\r\n      [nzCheckable]=\"true\"\r\n      (ngModelChange)=\"onChange($event)\">\r\n    </nz-tree-select>`\r\n})\r\n\r\nexport class NzDemoTreeSelectCheckableComponent implements OnInit {\r\n\r\n  value: string[] = ['10001', '10022'];\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title: 'root1',\r\n      key: '1001',\r\n      children: [\r\n        {\r\n          title: 'child1',\r\n          key: '10001',\r\n          children: [\r\n            {\r\n              title: 'child1.1',\r\n              key: '100011',\r\n              children: []\r\n            },\r\n            {\r\n              title: 'child1.2',\r\n              key: '100012',\r\n              children: [\r\n                {\r\n                  title: 'grandchild1.2.1',\r\n                  key: '1000121',\r\n                  isLeaf: true,\r\n                  disabled: true\r\n                },\r\n                {\r\n                  title: 'grandchild1.2.2',\r\n                  key: '1000122',\r\n                  isLeaf: true\r\n                }\r\n              ]\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    }),\r\n    new NzTreeNode({\r\n      title: 'root2',\r\n      key: '1002',\r\n      children: [\r\n        {\r\n          title: 'child2.1',\r\n          key: '10021',\r\n          children: [],\r\n          disableCheckbox: true\r\n        },\r\n        {\r\n          title: 'child2.2',\r\n          key: '10022',\r\n          children: [\r\n            {\r\n              title: 'grandchild2.2.1',\r\n              key: '100221',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  onChange($event: NzTreeNode): void {\r\n    console.log($event);\r\n  }\r\n\r\n  ngOnInit(): void {\r\n  }\r\n}\r\n"