module.exports = "import { Component, OnInit } from '@angular/core';\r\nimport { NzFormatEmitEvent, NzTreeNode } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tree-select-async',\r\n  template: `\r\n    <nz-tree-select style=\"width: 250px\"\r\n                    nzPlaceHolder=\"Please select\"\r\n                    [nzDefaultExpandedKeys]=\"expandKeys\"\r\n                    [nzDropdownMatchSelectWidth]=\"true\"\r\n                    [nzDropdownStyle]=\"{ 'max-height': '300px' }\"\r\n                    [(ngModel)]=\"value\"\r\n                    [nzNodes]=\"nodes\"\r\n                    [nzAsyncData]=\"true\"\r\n                    (nzExpandChange)=\"onExpandChange($event)\">\r\n    </nz-tree-select>\r\n  `\r\n})\r\n\r\nexport class NzDemoTreeSelectAsyncComponent implements OnInit {\r\n  expandKeys = [ '1001', '10001' ];\r\n  value: string;\r\n  nodes = [\r\n    new NzTreeNode({\r\n      title: 'root1',\r\n      key: '1001',\r\n      children: [\r\n        {\r\n          title: 'child1',\r\n          key: '10001',\r\n          children: [\r\n            {\r\n              title: 'child1.1',\r\n              key: '100011',\r\n              children: []\r\n            },\r\n            {\r\n              title: 'child1.2',\r\n              key: '100012',\r\n              children: [\r\n                {\r\n                  title: 'grandchild1.2.1',\r\n                  key: '1000121',\r\n                  isLeaf: true,\r\n                  disabled: true\r\n                },\r\n                {\r\n                  title: 'grandchild1.2.2',\r\n                  key: '1000122',\r\n                  isLeaf: true\r\n                }\r\n              ]\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    }),\r\n    new NzTreeNode({\r\n      title: 'root2',\r\n      key: '1002',\r\n      children: [\r\n        {\r\n          title: 'child2.1',\r\n          key: '10021',\r\n          children: [],\r\n          disabled: true\r\n        },\r\n        {\r\n          title: 'child2.2',\r\n          key: '10022',\r\n          children: [\r\n            {\r\n              title: 'grandchild2.2.1',\r\n              key: '100221',\r\n              isLeaf: true\r\n            }\r\n          ]\r\n        }\r\n      ]\r\n    })\r\n  ];\r\n\r\n  onExpandChange(e: NzFormatEmitEvent): void {\r\n    if (e.node.getChildren().length === 0 && e.node.isExpanded) {\r\n      this.loadNode().then(data => {\r\n        e.node.addChildren(data);\r\n      });\r\n    }\r\n  }\r\n\r\n  loadNode(): Promise<any[]> {\r\n    return new Promise(resolve => {\r\n      setTimeout(() => resolve([\r\n          new NzTreeNode({\r\n            title: 'root2',\r\n            key: '10030-' + (new Date()).getTime(),\r\n            children: [\r\n              {\r\n                title: 'child2.1',\r\n                key: '10021',\r\n                children: [],\r\n                checked: true,\r\n                disabled: true\r\n              },\r\n              {\r\n                title: 'child2.2',\r\n                key: '10022',\r\n                children: [\r\n                  {\r\n                    title: 'grandchild2.2.1',\r\n                    key: '100221',\r\n                    isLeaf: true\r\n                  }\r\n                ]\r\n              }\r\n            ]\r\n          }),\r\n          {\r\n            title: 'childAdd-1',\r\n            key: '10031-' + (new Date()).getTime()\r\n          },\r\n          {\r\n            title: 'childAdd-2',\r\n            key: '10032-' + (new Date()).getTime(),\r\n            isLeaf: true\r\n          }]),\r\n        1000);\r\n    });\r\n  }\r\n\r\n  ngOnInit(): void {\r\n  }\r\n}\r\n"